import { OneSignal } from "@ionic-native/oneSignal";
import { Toast } from "@ionic-native/toast";
import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";
import { Storage } from "@ionic/storage";
import { NavController, LoadingController } from "ionic-angular";
import { Facebook, FacebookLoginResponse } from "@ionic-native/facebook";


@Component({
  selector: "page-landing",
  templateUrl: "landing.html"
})
export class LandingPage {
  login: boolean = true;
  forgotPassword: Boolean = false;
  email: string;
  name: string;
  fbid: string;

  loginnEmail: String;
  loginnPassword: String;

  onesignal: String;

  constructor(
    private facebook: Facebook,
    public navCtrl: NavController,
    public storage: Storage,
    public http: HttpClient,
    public loadingCtrl: LoadingController,
    public toast: Toast,
    private oneSignal: OneSignal
  ) {
    this.oneSignal.getIds().then(data => {
      this.onesignal = data.userId;
    });
  }

  ionViewWillEnter() {
    // this.http.get('http://127.0.0.1:8000/api/v1/sermons/all').subscribe((data) => {
    //   this.storage.ready().then(() => {
    //     this.storage.set('sermons', data.json().success);
    //   });
    // });
    //this.http.get('http://127.0.0.1:8000/api/v1/sermons/blogs').subscribe((blogs) => {
    //console.log(blogs.json());
    // this.storage.ready().then(() => {
    //   this.storage.set('blogs', blogs.json().success);
    // });
    //});
  }

  skipHome() {
    this.storage.ready().then(() => {
      this.navCtrl.setRoot("TabsPage");
    });
  }

  swapLogin(status: string) {
    if (status == "sign") {
      this.login = true;
    }
    if (status == "log") {
      this.login = false;
    }
  }

  fbLogin() {
    let loader = this.loadingCtrl.create({
      spinner: "dots",
      content: "Authenticating..."
    });
    this.facebook
      .login(["email", "public_profile"])
      .then((result: FacebookLoginResponse) => {
        console.log("fb login", result);
        this.facebook
          .api(
            "/" + result.authResponse.userID + "?fields=id,name,email,picture",
            []
          )
          .then(data => {
            console.log("fb", data);
            loader.present();
            this.http
              .post("https://realm-glory.herokuapp.com/api/auth/fb", {
                fbid: data.id,
                name: data.name,
                email: data.email,
                photo: data.picture.data.url,
                onesignal: this.onesignal
              })
              .subscribe(
                status => {
                  loader.dismiss();
                  this.storage.set("user", status["data"]);
                  this.navCtrl.setRoot("TabsPage");
                },
                err => {
                  loader.dismiss();
                  this.toast
                    .showLongBottom("Network Error, try again.")
                    .subscribe(() => {});
                  console.log("err", err);
                }
              );
          })
          .catch(err => console.log("err fb api", err));
      });
  }

  emailLogin() {
    let loader = this.loadingCtrl.create({
      spinner: "dots",
      content: "Authenticating..."
    });
    loader.present();

    console.log(this.loginnEmail);

    this.http
      .post("https://realm-glory.herokuapp.com/api/auth/login", {
        email: this.loginnEmail,
        password: this.loginnPassword
      })
      .subscribe(
        data => {
          console.log("login", data);
          loader.dismiss();
          if (data["status"] === "success") {
            this.storage.set("user", data["data"]);
            this.navCtrl.setRoot("TabsPage");
          } else {
            this.toast.showLongBottom(data["msg"]).subscribe(_ => {});
          }
        },
        err => {
          loader.dismiss();
          console.log(JSON.stringify(err));
        }
      );
  }

  emailSignup() {
    this.navCtrl.push("RegisterPage", { email: this.email });
  }

  forgotEmail() {
    let loader = this.loadingCtrl.create({
      spinner: "dots",
      content: "Please wait..."
    });
    loader.present();

    this.http
      .post("https://realm-glory.herokuapp.com/api/auth/forgotpass", {
        email: this.loginnEmail
      })
      .subscribe(
        data => {
          if (status === "success") {
            loader.dismiss();
            this.toast
              .showLongBottom("Check your email for your new password")
              .subscribe(() => {});
          } else {
            loader.dismiss();
            this.toast
              .showLongBottom("Email address is not registered.")
              .subscribe(() => {});
          }
        },
        err => {
          console.log("err", err);
          loader.dismiss();
        }
      );
  }
}



